import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';
import { BASE_URL } from '../config';
import type {
    UserStatusResponse,
    UserLoginPayload,
} from '../types/api/accountApiTypes';

const loginApiSlice = createApi({
    reducerPath: 'accountApi',
    baseQuery: fetchBaseQuery({
        baseUrl: `${BASE_URL}/account/`,
        credentials: 'include',
    }),
    endpoints: (builder) => ({
        getUserStatus: builder.query<UserStatusResponse, void>({
            query: () => 'check-user-status/',
        }),
        login: builder.mutation<void, UserLoginPayload>({
            query: (payload) => ({
                url: 'login/',
                method: 'POST',
                body: payload,
            }),
        }),
        logout: builder.mutation<void, void>({
            query: () => ({
                url: 'logout/',
                method: 'POST',
            }),
        }),
        refreshToken: builder.mutation<void, void>({
            query: () => ({
                url: 'token/refresh/',
                method: 'POST',
            }),
        }),
    }),
});

export const {
    useGetUserStatusQuery,
    useLoginMutation,
    useLogoutMutation,
    useRefreshTokenMutation,
} = loginApiSlice;
export default loginApiSlice;
